import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Nav, AlertController } from 'ionic-angular';
import { DatePicker } from '@ionic-native/date-picker/ngx';
// import { admissionDetails } from '../../module/admissionDetails';
import { environment } from '../../environment/environment'
import { ArEditPage } from '../ar-edit/ar-edit'
import { NetworkProvider } from '../../providers/network/network';
import { LoadingController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { ArCreatePage } from '../ar-create/ar-create';





/**
 * Generated class for the LandingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-landing',
  templateUrl: 'landing.html',
})
export class LandingPage {
  public networkStatus: any;
  public onlineOffline: boolean = navigator.onLine;
  public dateOfToday = new Date().toISOString().slice(0, 10)
  public baseURL = environment.baseUrl
  public findArUrl = "/api/v1/accidentRegisters/arLists"
  public localStorage
  public rows: any = []
  public arLists: any = [];
  public options;
  public editData;
  public loader;
  tablestyle = 'bootstrap';
  public findArDetails = {
    currentUserID: '',
    admissionDate: ''? this.dateOfToday:this.dateOfToday
  }
  localArListsLength: any;
  ionViewDidLoad() {
    this.getLocalStorage();
    setInterval(() => { this.checkInternet(); this.checkLocalStorage() }, 1000)
    // setInterval(()=>{this.findAdmissions()},5000)
  }

  // admissionDetails = {} as admissionDetails
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public dataService: NetworkProvider,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public nav: Nav,
    public storage: Storage,
    private datePicker: DatePicker) { }
  alert(message) {
    let alert = this.alertCtrl.create({
      subTitle: message,
    });
    setTimeout(() => alert.present(), 1000 / 1000);
    setTimeout(() => alert.dismiss(), 3000 / 2);
  }
  loaderFunction() {
    this.loader = this.loadingCtrl.create({
      content: `Please Wait...`,
    });
  }
  getLocalStorage() {
    this.localStorage = JSON.parse(localStorage.getItem('currentUser'))
    this.findArDetails.currentUserID = this.localStorage.userDetails._id;
    this.findAdmissions()
  }

  checkInternet() {
    if (!navigator.onLine) {
      this.networkStatus = 'Offline'
    } else {
      this.networkStatus = 'Online'
    }
  }
  checkLocalStorage(){
    this.storage.get('arLists').then((data)=>{
      if(data && data!=null && data!=undefined){
        this.localArListsLength=data.length
      }else{
        this.localArListsLength=0
      }
    })
  }

  newReg() {
    // this.navCtrl.setRoot(GeneralRegisterPage);
    this.navCtrl.setRoot(ArCreatePage);
  }

  goEdit(value) {
    this.loaderFunction();
    this.loader.present();
    let _id = value
    for (let i = 0; i < this.arLists.length; i++) {
      if (this.arLists[i]._id == _id) {
        this.editData = this.arLists[i]
      }
    }
    console.log(this.editData)
    this.loader.dismiss();
    this.navCtrl.push(ArEditPage, this.editData)
  }

  findAdmissions() {
    if (!navigator.onLine) {
      this.alert('No Internet Connection')
    } else {
      let data = {
        currentUserID: this.findArDetails.currentUserID,
        admissionDate: this.findArDetails.admissionDate == '' ? this.dateOfToday : this.findArDetails.admissionDate
      }
      this.loaderFunction();
      this.loader.present();
      this.dataService.postData(this.baseURL + this.findArUrl, data)
        .subscribe((res) => {
          console.log(res);
          this.rows = []
          this.arLists = res;
          this.alert(this.arLists.status)
          if (this.arLists && this.arLists.data) {
            this.arLists = this.arLists['data'];
            this.arLists.forEach(lists => {
              if(lists.patientInformation){
                let data = {
                  _id: lists._id,
                  name: lists.patientInformation.name==null||undefined  ? '':lists.patientInformation.name,
                  age: lists.patientInformation.age==null ||undefined ? '':lists.patientInformation.age,
                  gender: lists.patientInformation.gender==null ||undefined? '':lists.patientInformation.gender
                }
                this.rows.push(data)
              }
            });
          }
          console.log(this.rows)
          this.loader.dismiss();

        })
    }
  }
}




