import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import {LoginPage} from '../pages/login/login';
import {Storage} from '@ionic/storage'
import { NetworkProvider } from '../providers/network/network';
import {environment} from '../environment/environment'
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = HomePage;
  public baseUrl=environment.baseUrl
  public arCreateUrl="/api/v1/accidentRegisters/addAR"
  public onlineOffline:boolean = navigator.onLine;
  localArListData: any;
  networkStatus: string;
  constructor(platform: Platform,
              public storage:Storage,
              public dataService:NetworkProvider,
              statusBar: StatusBar,
              splashScreen: SplashScreen) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });
    setInterval(() => { this.checkInternet()},1000)
    
    setInterval(()=>{this.createArList()},20000)
  }
  checkInternet() {
    if (!navigator.onLine) {
      this.networkStatus = 'Offline'
    } else {
      this.networkStatus = 'Online'
    }
  }
  createArList(){
    if(this.networkStatus=='Online'){
      this.localArListData=[];
      this.storage.get('arLists').then((data)=>{
        this.localArListData=data
        if(this.localArListData!=null && this.localArListData!=undefined){
          this.dataService.postData(this.baseUrl+this.arCreateUrl,this.localArListData).subscribe(res=>{
            console.log(res);
            if(res['code']==200){
              this.storage.remove('arLists')
            }
          })
        }
      })

    }
  }
}




