import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,Nav,AlertController } from 'ionic-angular';
import {HomePage} from '../home/home';
import { Login } from '../../module/loginDetails';
import { LandingPage } from '../landing/landing';
import {MenuPage} from '../menu/menu'
import {environment} from '../../environment/environment'
import { NetworkProvider } from '../../providers/network/network';
import { LoadingController } from 'ionic-angular';
import swal from 'sweetalert'



/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  showDMS_1='0';
  showDMS_2='0';
  public baseURL=environment.baseUrl
  public loginUrl="/api/v1/hospitals/hospitalLogin"
  public findHospitalUrl="/api/v1/hospitals/findHospitals"
  public errMsg={
    message:''
  }
  selectedHospital:any ='';
  login={
    hospitalNumber:'',
    password:''
  }
  public DMS:any=[];
  public DME:any=[];
  public nonGovt:any=[];
  public curentUser:any
  public options
  public loader:any
 

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public nav:Nav,
              public alertCtrl:AlertController,
              public loadingCtrl:LoadingController,
              public dataService:NetworkProvider) {}


  alert(message){
     this.alertCtrl.create({
       title:'Info',
       subTitle:message,
       buttons:['OK']
     }).present()
  }
  loaderFunction(){
    this.loader = this.loadingCtrl.create({
      content: `Please Wait...`,
  });
  }  
  
  onChangeHospital(hospital){
    this.selectedHospital = hospital;
  }

  ionViewDidLoad() {}

  async onLogin(){
    this.loaderFunction()
    this.loader.present();
    this.dataService.postData(this.baseURL+this.loginUrl,this.login).subscribe((res) => {
      this.curentUser=res;
      this.loader.dismiss();
        if(this.curentUser.code===200){
          localStorage.setItem('currentUser', JSON.stringify({ token: this.curentUser.token,userDetails:this.curentUser.data}));
        this.nav.setRoot(MenuPage)
        } else{
          this.alert(this.curentUser.status)
        }
       
    })
  }

  findHospitals(value){
    this.loaderFunction();
     let data={
       "district":value
     }
    this.selectedHospital = '';
    this.login.hospitalNumber='';
    this.DME=[];
    this.DMS=[]
    this.nonGovt=[];
    this.loader.present();
    this.dataService.postData(this.baseURL+this.findHospitalUrl,data).subscribe((res) => {
      console.log(res)
      this.loader.dismiss();
      let hospitals=res
       if(hospitals['code']==200 && hospitals['data'].length!=0){
        for(let i=0;i<hospitals['data'].length;i++){
          if(hospitals['data'][i].hospitalDirectorate==="DMS"){
            this.DMS.push(hospitals['data'][i])
          }else if(hospitals['data'][i].hospitalDirectorate==="DME"){
            this.DME.push(hospitals['data'][i])
          }else if(hospitals['data'][i].hospitalDirectorate==="Non Govt"){
            this.nonGovt.push(hospitals['data'][i])
          }
         }
       }else{
         this.alert(res['status'])
       }
    })
  }
  districts=[
    'Test',
    'Chennai',
    'Cuddalore',
    'Kancheepuram',
    'Thiruvallur',
    'Thiruvannamalai',
    'Vellore',
    'Villuppuram',
    'Kallakurichi',
    'Dharmapuri',
    'Krishnagiri',
    'Namakkal',
    'Salem',
    'Erode',
    'Thiruppur',
    'Coimbatore',
    'Nilgiris',
    'Madurai',
    'Theni',
    'Dindigul',
    'Sivagangai',
    'Virudhunagar',
    'Ramanathapuram',
    'Thirunelveli',
    'Thoothukudi',
    'Kanyakumari',
    'Pudhukkottai',
    'Ariyalur',
    'Nagapattinam',
    'Perambalur',
    'Thanjavur',
    'Thiruchirappalli',
    'Karur',
    'Thiruvarur'

]
}



